.page1 {
  background: url(../images/p1-bg.jpg) no-repeat;
  background-size: 100% 100%;
}
.page2 {
  background: url(../images/p2-bg.jpg) no-repeat;
  background-size: 100% 100%;
}
.page2 h1 {
  width: 100%;
  padding-top: 20%;
  text-align: center;
}
.page2 h1 img {
  width: 34.21875%;
}
.page2 .enroll {
  padding-top: 10%;
}
.page2 .enroll li {
  width: 70%;
  margin: 0 auto;
  height: 38px;
  margin-bottom: 10px;
  line-height: 38px;
}
.page2 .enroll li:before,
.page2 .enroll li:after {
  display: table;
  content: "";
}
.page2 .enroll li:after {
  clear: both;
}
.page2 .enroll li span {
  float: left;
  width: 35%;
  padding-right: 5px;
  text-align: right;
}
.page2 .enroll li span img {
  max-width: 100%;
  vertical-align: middle;
}
.page2 .enroll li .textstyle {
  width: 100%;
  height: 36px;
  padding-left: 10px;
  line-height: 36px;
  border: solid 1px #7c797a;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  font-family: 微软雅黑;
}
.page2 .btn-cont {
  padding-top: 0;
  text-align: center;
}
.page2 .btn-cont .btn {
  display: inline-block;
  width: 70%;
  height: 38px;
  line-height: 38px;
  padding: 0;
  margin: 0;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  color: #fff;
  background: #ffef00;
  font-size: 16px;
  font-family: 微软雅黑;
  text-align: center;
  color: #000;
  font-weight: bold;
}
.page2 .btn-cont .btn-e {
  margin: 0;
  background: #ffef00;
}
.container {
  width: 100%;
  height: 100%;
  background: url(../images/qd-bg.jpg) no-repeat;
  background-size: 100% 100%;
}
.sign {
  width: 100%;
  padding-top: 50%;
  margin: 0 auto;
  text-align: center;
}
.sign-img1 {
  max-width: 29.53125%;
}
.sign-img2 {
  max-width: 38.4375%;
  margin-top: 10px;
}
.sign-point {
  display: none;
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}
.sign-success {
  display: none;
  width: 100%;
  height: 100%;
  background: url(../images/success-bg.jpg) no-repeat;
  background-size: 100% 100%;
  text-align: center;
}
.sign-success img {
  max-width: 51.71875%;
  margin-top: 50%;
}
.no-sign {
  display: none;
  width: 100%;
  height: 100%;
  padding: 0 10%;
  background: url(../images/no-success.jpg) no-repeat;
  background-size: 100% 100%;
}
.no-sign p {
  color: #000;
  font-size: 16px;
  line-height: 20px;
  word-break: break-all;
}
.no-sign p a {
  color: #009fe8;
}
.no-sign img {
  max-width: 54.84375%;
  margin-top: 20%;
  margin-bottom: 20px;
}
.tip-layer {
  position: absolute;
  z-index: 999;
  display: none;
  width: 70%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: #595757;
  color: #fff;
  font-size: 16px;
  -webkit-box-shadow: 1px 1px 3px rgba(55, 55, 55, 0.7);
  box-shadow: 1px 1px 3px rgba(55, 55, 55, 0.7);
  border-radius: 3px;
}
.plus-tip {
  position: absolute;
  right: -20px;
  top: 2px;
  height: 16px;
  line-height: 1;
  font-family: Arial;
  font-size: 11px;
  color: #ff4800;
  font-weight: normal;
  font-style: normal;
  animation: show-up 0.6s ease-in-out both;
  -webkit-animation: show-up 0.6s ease-in-out both;
}
/* 加载画面 */
.loading {
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  text-align: center;
}
.loading p {
  position: absolute;
  width: 100%;
  top: 30%;
  display: block;
  line-height: 25px;
  color: #fff;
  text-align: center;
  font-size: 12px;
  background: no-repeat;
}
.loading p img {
  display: block;
  margin: 0 auto;
  max-width: 25%;
}
.loading p .load-bg {
  -webkit-animation: rotate 0.8s infinite both;
  -moz-animation: rotate 0.8s infinite both;
  -ms-animation: rotate 0.8s infinite both;
  -o-animation: rotate 0.8s infinite both;
  animation: rotate 0.8s infinite both;
}
.loading p .load-img {
  margin-top: 30px;
  -webkit-animation: show 0.8s infinite both;
  -moz-animation: show 0.8s infinite both;
  -ms-animation: show 0.8s infinite both;
  -o-animation: show 0.8s infinite both;
  animation: show 0.8s infinite both;
}
@-webkit-keyframes show {
  0% {
    -webkit-opacity: 0;
  }
  100% {
    -webkit-opacity: 1;
  }
}
@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes rotate {
  0% {
    opacity: 0;
    -webkit-transform: rotate(0);
  }
  100% {
    opacity: 1;
    -webkit-transform: rotate(360deg);
  }
}
@keyframes rotate {
  0% {
    opacity: 0;
    transform: rotate(0);
  }
  100% {
    opacity: 1;
    transform: rotate(360deg);
  }
}
@-webkit-keyframes show-up {
  from {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
  to {
    -webkit-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    -o-transform: translateY(-20px);
    transform: translateY(-20px);
    opacity: 0;
  }
}
@-o-keyframes show-up {
  from {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
  to {
    -webkit-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    -o-transform: translateY(-20px);
    transform: translateY(-20px);
    opacity: 0;
  }
}
@-moz-keyframes show-up {
  from {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
  to {
    -webkit-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    -o-transform: translateY(-20px);
    transform: translateY(-20px);
    opacity: 0;
  }
}
@keyframes show-up {
  from {
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
  to {
    -webkit-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    -o-transform: translateY(-20px);
    transform: translateY(-20px);
    opacity: 0;
  }
}
@media screen and (min-width: 321px) and (max-width: 360px) and (max-height: 519px) and (-webkit-min-device-pixel-ratio: 1) {
  .sign {
    padding-top: 47%;
  }
}
@media screen and (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3) {
  .sign {
    padding-top: 50%;
  }
}
@media screen and (max-height: 420px) {
  .page2 h1 {
    padding-top: 12%;
  }
  .sign {
    padding-top: 39%;
  }
}



